                more:false, //是否显示
                height:document.documentElement.clientHeight-40+"px"
            }
        },
        methods:{
            disOpen:function () {
                if(this.dis||this.community||this.more){
                    this.dis=this.community=this.more=false;
                }else{
                    this.dis=true;
                }
            },
            comOpen:function () {
                if(this.community){
                    this.dis=true;
                    this.community=false;
                }else{
                    this.dis=false;
                    this.community=true;
                }
            },
            moreOpen:function () {
                if(this.more){
