import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from '../components/HomePage'
import SignUp from '../components/SingUp'

Vue.use(VueRouter)

export default new VueRouter({
    routes:[
        {
            path:"/",
            name:"HomePage",
            component:HomePage,
            meta:{
                title:"响应式_Dribbble首页"
            }
        },{
            path:"/signup",
            name:"SignUp",
            component:SignUp,
            meta:{
                title:"响应式_Dribbble注册页"
            }
        }
        ]
})