<template>
    <div class="rongqi">
    <div class="tabs">
        <ul>
            <li>
                <a @click="Popular=!Popular">Popular</a>
                <ul v-show="Popular">
                    <li><a>Recent</a></li>
                    <li><a>Most Viewed</a></li>
                    <li><a>Most Commented</a></li>
                </ul>
            </li>
            <li>
                <a @click="Shots=!Shots">Shots</a>
                <ul v-show="Shots">
                    <li><a>Debuts</a></li>
                    <li><a>Team Shots</a></li>
                    <li><a>Playoffs</a></li>
                    <li><a>Rebounds</a></li>
                    <li><a>Animated GIFs</a></li>
                    <li><a>Videos</a></li>
                    <li><a>Shots with Attachments</a></li>
                </ul>
            </li>
            <li>
                <a @click="Now=!Now">Now</a>
                <ul v-show="Now">
                    <li><a>This Past Week</a></li>
                    <li><a>This Past Month</a></li>
                    <li><a>This Past Year</a></li>
                    <li><a>All Time</a></li>
                </ul>
            </li>
        </ul>
        <div>
            <a class="actions"></a>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: "Tabs",
        data:function(){
          return {
              Popular:false,
              Shots:false,
              Now:false
          }
        },
        methods:{

        },
        watch:{
            Popular:function () {
                if(this.Popular){this.Shots=this.Now=false;}
            },
            Shots:function () {
                if(this.Shots){this.Popular=this.Now=false;}
            },
            Now:function () {
                if(this.Now){this.Shots=this.Popular=false;}
            }
        }
    }
</script>

<style scoped>
    .tabs{width: 100%;position: absolute;border-bottom: 1px solid #e5e5e5;text-align: center;}
    .tabs>ul>li{display: inline-block;margin:0 5px;}
    .tabs>ul>li>a{display: inline-block;font-weight: 500;padding-right:15px;color: #444;background-image: url("../assets/icon-dd-arrow.png");background-repeat: no-repeat;background-position: right center;}
    .tabs>ul>li>ul{position:absolute;text-align: left;color:#999;margin: -5px 0 0 -15px;padding: 6px 0 10px 0;background: #fbfbfb;border-radius: 4px;border-bottom-right-radius: 4px;font-size: 13px;}
    .tabs>ul>li>ul>li{padding: 5px 15px;line-height: 1.25;}
    .tabs>ul>li>ul>li:hover{color: #444;background-color: #eee;}
    /*.tabs>ul>li:hover>ul{display: block;}*/
    .tabs .actions{display: inline-block;width: 16px;height: 16px;background-image:url("../assets/icon-shot.png");background-repeat: no-repeat;background-size: 16px;position: absolute;top:50%;margin-top: -8px;}
    @media (min-width:769px) {
        .rongqi{height: 50px;}
        .tabs>ul>li>a{height: 50px;line-height: 50px;font-size: 14px;}
        .tabs>ul>li>ul{width: 200px;}
        .tabs .actions{right: 30px;}
    }
    @media (max-width:768px) {
        .rongqi{height: 36px;}
        .tabs>ul>li>a{height: 36px;line-height: 36px;font-size: 13px;}
        .tabs>ul>li>ul{width: 100%;left: 15px;}
        .tabs .actions{right: 10px;}
    }
</style>